import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, EquipamentoFiscal } from '../../../../models/dtos/ger/equipamentoFiscal';
import { TIPO_EQUIPAMENTO_FISCAL_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoEquipamentoFiscalEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface EquipamentoFiscalEditViewProps extends EditViewProps<EquipamentoFiscal> {}

const EquipamentoFiscalEditView: FC<EquipamentoFiscalEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const { reset, ...formProps } = useForm<EquipamentoFiscal>({ defaultValues: selectedItem, mode: 'onBlur' });

  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  useEffect(
    () => {
      const { empresa } = globalReducer.globalParameter;
      setTimeout(() => {
        formProps.setValue('empresa', empresa);
      }, 800);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Equipamentos Fiscais">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="empresa.id" hidden={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 8) } }}
              label="Número Equipamento"
              id="nroEquipamento"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              placeholder="Informe o Número Equipamento."
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática', placement: 'bottom' }}
              type={InputType.NUMBER}
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 20 } }}
              id="nroSerie"
              isPrependLabel={true}
              label="Número Série"
              placeholder="Informe o Número Série"
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoInt"
              inputDropDownProps={{ optionsProvider: TIPO_EQUIPAMENTO_FISCAL_ENUM_VALUES, selectProps: { isClearable: false } }}
              label="Tipo Equipamento"
              isPrependLabel={true}
              placeholder="Informe o Tipo Equipamento"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default EquipamentoFiscalEditView;
